import * as React from "react";

import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import logo from "../Resources/logo3.png";
import Grid from "@mui/material/Grid";

export default function Text({ title, years }) {
  const newDate = new Date();
  const thisYear = newDate.getFullYear();
  return (
    <Grid
      container
      direction="column"
      justifyContent="center"
      alignItems="center"
    >
      <Box paddingTop="2%" component="img" sx={{ width: "50%" }} src={logo} />
      <Typography variant="h6" paddingTop="2%" paddingX="10%" height="70px">
        On this day {thisYear - years} {years > 1 ? "years" : "year"} ago{" "}
        {title} was released
      </Typography>
    </Grid>
  );
}
