import * as React from "react";

import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
import AddIcon from "@mui/icons-material/Add";
import RemoveIcon from "@mui/icons-material/Remove";

import IconButton from "@mui/material/IconButton";
import Grid from "@mui/material/Grid";

export default function Controler({ setPlaying, setIndex, max }) {
  return (
    <Grid container direction="row" justifyContent="center" alignItems="center">
      <IconButton
        onClick={() => setPlaying(true)}
        size="large"
        color="inherit"
        aria-label="play"
      >
        <PlayArrowIcon />
      </IconButton>
      <IconButton
        onClick={() => setPlaying(false)}
        size="large"
        color="inherit"
        aria-label="play"
      >
        <PauseIcon />
      </IconButton>

      <IconButton
        onClick={() => setIndex((prev) => (prev - 1 < 0 ? max : prev - 1))}
        size="large"
        color="inherit"
        aria-label="play"
      >
        <RemoveIcon />
      </IconButton>
      <IconButton
        onClick={() => setIndex((prev) => (prev + 1 > max ? 0 : prev + 1))}
        size="large"
        color="inherit"
        aria-label="plus"
      >
        <AddIcon />
      </IconButton>
    </Grid>
  );
}
